// /src/scss/_fonts.scss

// for example
// ../fonts/Pragmatica/PragmaticaBlack/PragmaticaBlack.eot
// ../fonts/$font-name/$font-folder/$file-name.eot

// $font-family: ProximaNova, sans-serif;
$font-name: ProximaNova;
@each $font-folder, $file-name, $font-weight, $font-style in  (ProximaNovaThin, 'ProximaNovaThin', 100, normal),
                                                              (ProximaNovaThinItalic, 'ProximaNovaThinItalic', 100, italic),
                                                              (ProximaNovaLight, 'ProximaNovaLight', 300, normal),
                                                              (ProximaNovaLightItalic, 'ProximaNovaLightItalic', 300, italic),
                                                              (ProximaNovaRegular, 'ProximaNovaRegular', 400, normal),
                                                              (ProximaNovaRegularItalic, 'ProximaNovaRegularItalic', 400, italic),
                                                              // Шрифтов веса 500 нет, но в природе существуют
                                                              // (ProximaNovaMedium , 'Proxima Nova Medium', 500, normal),
                                                              // (ProximaNovaMediumItalic , 'Proxima Nova Medium Italic', 500, italic),
                                                              (ProximaNovaSemibold, 'ProximaNovaSemibold', 600, normal),
                                                              (ProximaNovaSemiboldItalic, 'ProximaNovaSemiboldItalic', 600, italic),
                                                              (ProximaNovaBold, 'ProximaNovaBold', 700, normal),
                                                              (ProximaNovaBoldItalic, 'ProximaNovaBoldItalic', 700, italic),
                                                              (ProximaNovaExtrabold, 'ProximaNovaExtrabold', 800, normal),
                                                              (ProximaNovaExtraboldItalic, 'ProximaNovaExtraboldItalic', 800, italic),
                                                              (ProximaNovaBlack, 'ProximaNovaBlack', 900, normal),
                                                              (ProximaNovaBlackItalic, 'ProximaNovaBlackItalic', 900, italic) {
  @font-face {
    font-family: $font-name;
    font-weight: $font-weight;
    font-style: $font-style;

    src: url(/fonts/#{$font-name}/#{$font-folder}/#{$file-name}.eot); /* IE9 Compat Modes */
    src: url(/fonts/#{$font-name}/#{$font-folder}/#{$file-name}.eot?#iefix) format('embedded-opentype'), /* IE6-IE8 */
    // url(/fonts/#{$font-name}/#{$font-folder}/#{$file-name}.woff2) format('woff2'), /* Modern Browsers */
    url(/fonts/#{$font-name}/#{$font-folder}/#{$file-name}.woff) format('woff'), /* Modern Browsers */
    url(/fonts/#{$font-name}/#{$font-folder}/#{$file-name}.ttf) format('truetype');
  }
}

@font-face {
  font-family: ALSRubl;
  src:  url('/fonts/Rouble/rouble.woff2') format('woff2'),
        url('/fonts/Rouble/rouble.woff') format('woff'),
        url('/fonts/Rouble/rouble.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: rub-arial-regular;
  src: url(data:font/woff;charset=utf-8;base64,)
}